<template>
  <div id="app">
    <!-- 路由中的内容加载一次之后放到内存 -->
    <keep-alive exclude="Detail">
      <!-- 显示当前路由地址所对应的内容 -->
      <router-view/>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted () {
    const { NODE_ENV } = process.env
    document.documentElement.className = NODE_ENV
  }
}
</script>

<style>
</style>



